import { Component, OnInit } from '@angular/core';

declare  var $:any;
@Component({
  selector: 'comment-page',
  templateUrl: './comment-page.component.html',
  styleUrls: ['./comment-page.component.css']
})
export class CommentPageComponent implements OnInit {

  constructor() {
  }

  ngOnInit() {
    var that = this;
    QC.Login({
      btnId:"qqLoginBtn",    //插入按钮的节点id
      showModal:"",	//Boolean, 可选，默认false	是否以Iframe窗口打开
      scope:"all",	//String可选，默认all	用户需要确认的scope授权项
      size:"A_XL",	//String可选，默认B_S	按钮尺寸 [A_XL, A_L, A_M, A_S, B_M, B_S, C_S]
    },that.loginBack,that.logoutBack);
    this.aa();
  }

  //登录成功回调函数
  loginBack(oInfo, oOpts){
    alert(oInfo.nickname); // 昵称
    alert(oOpts.btnId);    // 点击登录的按钮Id
    this.getInfo();
  }

  //注销成功回调函数
  logoutBack(){
    alert("注销成功!");
  }

  aa(){
    $.ajax({
      url: "http://gameinfo2.cosbox.99.com:8001/query/queryProfileAndHistory/",
      data:{"userId":"12242510"},
      timeout : 60000,  //超时时间设置，单位毫秒
      contentType: "application/json; charset=utf-8",
      //type:'post',
      cache:false,
      dataType:'jsonp',
      success:function(res) {
        console.log("res:",res);
      },
      error : function(res) {
        console.log("网络请求错误",res);
      },
    });
  }

  getInfo() {
    if(QC.Login.check()){
      QC.api("get_user_info")
        .success(function(s){//成功回调，通过s.data获取OpenAPI的返回数据
          //nick = s.data.nickname; //获得昵称
          //headurl = s.data.figureurl_qq_1; //获得头像
          QC.Login.getMe(function(openId, accessToken){
            console.log("openId:",openId);
            console.log("accessToken:",accessToken);
          })
        })
        .error(function(f){//失败回调
          alert("获取用户信息失败！登录失败！");
        })
        .complete(function(c){//完成请求回调
          //	alert("获取用户信息完成！");
        });
    }else{
      alert("请登录！");
    }
  }

}
